import React from 'react'
import Sketch from 'react-p5'

var lifespan = 200;
var count = 0;
var target;
var maxforce = 0.2;


function Population(p5) {
    this.rockets = [];
    this.popsize = 25;
    this.matingPool = [];

    for (var i = 0; i < this.popsize; i++) {
        this.rockets[i] = new Rocket(p5);
    }

    this.evaluate = function() {
        var maxfit = 0;
        for (let i = 0; i < this.popsize; i++) {
            this.rockets[i].calcFitness();
            if(this.rockets[i].fitness > maxfit){
                maxfit = this.rockets[i].fitness;
            }
        }
        for (let i = 0; i < this.popsize; i++) {
            this.rockets[i].fitness /= maxfit;

        }
        var n;
        this.matingPool = [];
        for (let i = 0; i < this.popsize; i++) {
            n = this.rockets[i].fitness * 10;
            for (let index = 0; index < n; index++) {
                this.matingPool.push(this.rockets[i].dna);
            }
        }
    }

    this.selection = function(p5) {
        var parentA, parentB, child;
        var newrockets = [];
        for (let index = 0; index < this.rockets.length; index++) {
        
            parentA = this.matingPool[Math.floor(Math.random()*this.matingPool.length)];
            parentB = this.matingPool[Math.floor(Math.random()*this.matingPool.length)];

            child = parentA.crossover(p5, parentB);
            child.mutation(p5);
            newrockets[index] = new Rocket(p5,child);
        }
        this.matingPool = [];
        this.rockets = newrockets;
    }

    this.run = function(p5) {
        for (var i = 0; i < this.popsize; i++) {
            this.rockets[i].update();
            // Displays rockets to screen
            this.rockets[i].show(p5);
        }
    }

}

function DNA(p5,genes) {
    if(genes) {
        this.genes = genes;
    }
    else{
        this.genes = [];
        for (var i = 0; i< lifespan; i++)
        {
            this.genes[i] = p5.createVector(Math.random()-0.5, Math.random()-0.5);
            this.genes[i].setMag(maxforce);
        }
    }


    this.crossover = function(p5, partner) {
        var newgenes = [];
        var mid = Math.floor(Math.random()*this.genes.length);
        for (let index = 0; index < this.genes.length; index++) {
            if(index>mid) {
                newgenes[index] = this.genes[index];
            }else {
                newgenes[index] = partner.genes[index];
            }
        }
        return new DNA(p5,newgenes);
    }
    this.mutation = function(p5) {
        for (var i = 0; i < this.genes.length; i++) {
          // if random number less than 0.01, new gene is then random vector
            if (p5.random(1) < 0.01) {
                this.genes[i] = p5.createVector(Math.random()-0.5, Math.random()-0.5);
                this.genes[i].setMag(maxforce);
            }
        }
    }
}

function Rocket(p5, dna) {
    this.pos = p5.createVector(p5.width/2, p5.height-30);
    this.vel = p5.createVector();
    this.acc = p5.createVector();
    this.completed = false;
    this.crashed = false;
    if(dna) {
        this.dna = dna;
    }else {
        this.dna = new DNA(p5);
    }
    this.fitness = 0;

    this.applyForce = function(force) {
        this.acc.add(force);
    }

    this.calcFitness = function() {
        var d = p5.dist(this.pos.x,this.pos.y, target.x, target.y);
        this.fitness = p5.map(d,0,p5.width, p5.width, 0);
        if (this.completed) {
            this.fitness *= 10;
        }
          // If rocket does not get to target decrease fitness
        if (this.crashed) {
            this.fitness /= 10;
        }
    }

    this.update = function() {
        var d = p5.dist(this.pos.x, this.pos.y, target.x, target.y);
        if(d<10){
            this.completed = true;
            this.pos = target.copy();
        }

        // Rocket has hit left or right of window
        if (this.pos.x > p5.width || this.pos.x < 0) {
            this.crashed = true;
        }
        // Rocket has hit top or bottom of window
        if (this.pos.y > p5.height || this.pos.y < 0) {
            this.crashed = true;
        }

        this.applyForce(this.dna.genes[count]);

        if (!this.completed && !this.crashed) {
            this.vel.add(this.acc);
            this.pos.add(this.vel);
            this.acc.mult(0);
            this.vel.limit(4);
        }
    }

    this.show = function(p5) {
        p5.push();
        p5.noStroke();
        p5.fill(255,150)
        p5.translate(this.pos.x, this.pos.y);
        p5.rotate(this.vel.heading());
        p5.rectMode(p5.CENTER);
        p5.rect(0,0,25,5);
        p5.pop();
    }

}

const SmartRockets = () => {

    var population;

    const setup = (p5, parent) => {
        p5.createCanvas(400,300).parent(parent);
        population = new Population(p5);
        target = p5.createVector(p5.width/2, 50);

    }

    const draw = p5 => {
        p5.background(0);
        population.run(p5);
        count++;

        if(count === lifespan) {
            population.evaluate();
            population.selection(p5);
            count = 0;
        }
        p5.ellipse(target.x, target.y, 16, 16);
    }

    return <Sketch setup={setup} draw={draw} />
}

export default SmartRockets;