import React from 'react'
import './Cartitem.css'

function Cartitem({id, name, price, qty, updateQty}) {
    const addOne = ()       => updateQty(id, parseInt(qty)+1)
    const subtractOne = ()  => updateQty(id, parseInt(qty)-1)

    function handleChange(event) {
        updateQty(id, event.target.value)
    }

    return (
        <div className="Cartitem">
            <div>{name}</div>
            <div>${price}</div>
            <div><button onClick={subtractOne} disabled={qty < 1}>-</button>
            <input type="number" min="0" value={qty} onChange={handleChange} />
            <button onClick={addOne}>+</button></div>
            <div className="totals">${qty*price}</div>
        </div>
    )
}

export default Cartitem