import React from 'react'
import Sketch from 'react-p5'


export default () => {
    var minVal = -2;
    var maxVal = 2;

    var minSlider;
    var maxSlider;
    var maxIterations;
    var pix;
    var a,b,twoab,n,z;
    var aa,bb;
    var bright;
    var ca,cb;
    var consta,constb; 
        // consta =-0.70176,constb=-0.3842;
        // consta =0,constb=-0.8;

    function setup(p5, parent) {
        p5.createCanvas(360, 360).parent(parent);
        p5.pixelDensity(1);
    }

    function draw(p5) {
        maxIterations = 100;
        p5.loadPixels();
        consta = p5.map(p5.mouseX,0,p5.width,-1,1);
        constb = p5.map(p5.mouseY,0,p5.height,-1,1);
        for (let x = 0; x < p5.width; x++) {
        for (let y = 0; y < p5.height; y++) {

            a = p5.map(x,0,p5.width,minVal,maxVal);
            b = p5.map(y,0,p5.height,minVal,maxVal);

            n=0;
            z=0;
            ca = a;
            cb = b;

            while(n<maxIterations){
                aa = a*a;
                bb = b*b;
                if(p5.abs(a+b)>16){
                break;
                }
                twoab = 2*a*b;
                a=aa - bb +consta;
                b=twoab +constb;
                n++;
            }
            bright = p5.map(n,0,maxIterations,0,1);
            bright = p5.map(p5.sqrt(bright),0,1,0,255);
            if(n===maxIterations){
                bright = 0;
            }


            pix = (x+y*p5.width)*4;
            p5.pixels[pix+0] = bright;
            p5.pixels[pix+1] = bright;
            p5.pixels[pix+2] = bright;
            p5.pixels[pix+3] = 255;
        }
        }

        p5.updatePixels();
    }

    return <Sketch setup={setup} draw={draw} />

}
