import React, {useState, useEffect} from 'react'
import Cartitem from './Cartitem'
import './Shoppingcart.css'


const Shoppingcart = ({initialItems}) => {
    const initialState = JSON.parse(window.localStorage.getItem('items'))
    const [items, setItems] = useState(initialState || initialItems)

    useEffect(() => {
        window.localStorage.setItem('items', JSON.stringify(items))
    }, [items])

    const updateQty = (id, newQty) => {
        const newItems = items.map (item => {
            if(item.id === id){
                return { ...item, qty: newQty}
            }
            return item
        })
        setItems(newItems)
    }

    const clearButton = () => {
        window.localStorage.clear();
        
        const newItems = items.map(element => {
            element.qty = 0;
            return element;
        });

        setItems(newItems);
    }

    const grandTotal = items.reduce((total, item) => (
        total + item.qty * item.price
    ), 0).toFixed(2)
    return (
        <div className="Shoppingcart">
            <h2 className="Cart-title">Shopping Cart</h2><hr/>
            <div className="Cartitems">
                {items.map(item => (
                    <Cartitem key={item.id} updateQty={updateQty} {...item} />
                ))}
            </div>
            <div className="Sc_Control">
                <button className="clearButton" onClick={clearButton}>Clear</button>
                <h4 className="grandTotal">Total: {grandTotal}</h4>
            </div>
        </div>
    )
}

export default Shoppingcart