import React from 'react'
import Sketch from 'react-p5'

var width = 600, height = 600;

var dV = 0.2;
var wind = Math.random()*5-2;
class Drop {
    constructor(p5) {
        this.x = Math.random()*width*1.5-Math.random()*width*0.75;
        this.y = -200+Math.random()*10;
        this.z = Math.random()*20;
        this.xspeed = wind;
        this.yspeed = p5.map(this.z,0,20,4,20);
        this.length = p5.map(this.z,0,20,10,20);
        this.explosions = [];
    }

    fall(p5) {
        this.y=this.y+this.yspeed;
        this.x=this.x+this.xspeed;
        
        this.yspeed += dV;
        if(this.y>height)
        {
        this.restart(p5);
        }
    }

    show(p5) {
        p5.strokeWeight(p5.map(this.z,0,20,0.6,1));
        p5.stroke(60,60,255);
        p5.line(this.x,this.y,this.x+wind,this.y+this.length);
    }

    restart(p5){
        this.x = Math.random()*width*1.5-Math.random()*width*0.75;
        this.y = -15+Math.random()*10;
        this.yspeed = p5.map(this.z,0,20,4,20);
    }

}

export default () => {
    var drops = [];
    var dropCount = 1000;
    var backgroundColor = [30,30,30];
    var time = 0;
    var timer;
    var textTime = 1;
    var textTimer = 0;
    var textOn = false;
    
    
    var quotes = [
    
        "Tyger Tyger, burning bright,\n" 
        +"In the forests of the night;\n" 
        +"What immortal hand or eye, \n"
        +"Could frame thy fearful symmetry?\n\n"
        
        +"In what distant deeps or skies. \n"
        +"Burnt the fire of thine eyes?\n"
        +"On what wings dare he aspire?\n"
        +"What the hand, dare seize the fire?\n\n"
        
        +"And what shoulder, & what art,\n"
        +"Could twist the sinews of thy heart?\n"
        +"And when thy heart began to beat,\n"
        +"What dread hand? & what dread feet?\n"
        
        +"What the hammer? what the chain,\n" 
        +"In what furnace was thy brain?\n"
        +"What the anvil? what dread grasp, \n"
        +"Dare its deadly terrors clasp! \n"
        
        +"When the stars threw down their spears\n"
        +"And water'd heaven with their tears: \n"
        +"Did he smile his work to see?\n"
        +"Did he who made the Lamb make thee?\n\n"
        
        +"Tyger Tyger burning bright, \n"
        +"In the forests of the night: \n"
        +"What immortal hand or eye,\n"
        +"Dare frame thy fearful symmetry?\n\n"
        +"The Tyger BY WILLIAM BLAKE",
    
    
        "It matters not what the clocks say or the attitudes and labors of men. Morning is when I am awake and there is a dawn in me. \nMoral reform is the effort to throw off sleep. Why is it that men give so poor an account of their day if they have not been slumbering? \nThey are not such poor calculators. If they had not been overcome with drowsiness, they would have performed something. \nThe millions are awake enough for physical labor; but only one in a million is awake enough for effective \nintellectual exertion, only one in a hundred millions to a poetic or divine life. \nTo be awake is to be alive. I have never yet met a man who was quite awake. How could I have looked him in the face?\n\n"
        +"Walden; or, Life in the Woods BY Henry David Thoreau",
    
    
        "Let your virtue be too high for the familiarity of names, and if you\n"
        +"must speak of it, be not ashamed to stammer about it.\n"
        +"Then speak and stammer: 'This is my good, this do I love, it pleases me'\n"
        +"entirely, only this way do I want the good.\n"
        +"Not as the law of a God do I desire it, not as a human law or a human\n"
        +"need do I want it; it shall not be a signpost for me to over-earths and\n"
        +"paradises.\n"
        +"An earthly virtue is what I love: it has little prudence, and least of all\n"
        +"the reason and wisdom of everyone.\n"
        +"But that bird built its nest beside me: therefore, I love and cherish it -\n"
        +"now it sits beside me on its golden eggs.\n"
        +"Thus should you stammer, and praise your virtue.\n\n"
        +"Thus Spoke Zarathustra",
    
        "Freedom means basically freedom to make mistakes. Freedom to be a damn fool",
    
    ]
    
    var count = Math.floor(Math.random()*(quotes.length-1));
    
    
    function setup(p5, parent) {
        p5.createCanvas(600,600).parent(parent);
        p5.textSize(10);
        p5.textAlign(p5.CENTER, p5.CENTER);
        for (let i = 0; i < dropCount; i++) {
            drops.push(new Drop(p5));
        }
        timer = Math.random()*500;
        textTimer = Math.random()*600+600;
        p5.fill(255);
    }
    
    function draw(p5) {
        time++;
        textTime++;
        doit(p5);
        quoteManager();
    }
    
    function doit(p5)
    {
    
        p5.background(backgroundColor);
        for (let i = 0; i < dropCount; i++) {
            drops[i].fall(p5);
            drops[i].show(p5);
        }
    
        if(time > timer && Math.random()>0.01)
        {
            timer = Math.random()*500;
            time = 0;
            backgroundColor[0] *=5;
            backgroundColor[1] *=5;
            backgroundColor[2] *=5;
        
        }else
        {
            backgroundColor[0] -= (backgroundColor[0]-30)/15;
            backgroundColor[1] -= (backgroundColor[1]-30)/15;
            backgroundColor[2] -= (backgroundColor[2]-30)/15;
        }
    
        if(textOn)
        {
    
    
            p5.text(quotes[count],300,300);
        }
    
    }
    
    
    function quoteManager(){
        if(textTime>textTimer)
        {
            textOn = true;
            count = (count+1)%quotes.length;
            textTime=0;
            textTimer = Math.random()*600+600;
        }
    }
    
    return <Sketch setup={setup} draw={draw} />


}

