import React, {useState} from 'react'
import Shoppingcart from './Shoppingcart'
import './Storage.css'
import Snake from './snake'
import Julia from './Julia'
import Rain from './Rain'
import SmartRockets from './SmartRockets'


const items = [
    { id: 1, name: 'Taco Seasoning', price: 2.25, qty: 0},
    { id: 2, name: 'Pinto Beans', price: 1.99, qty: 0},
    { id: 3, name: 'Sour Cream', price: 3.50, qty: 0},
    { id: 4, name: 'Bread', price: 0.50, qty: 0},

]

const Storage = () => {
    const [sCartOn, setSCartOn] = useState(true);
    const [snakeOn, setSnakeOn] = useState(true);
    const [juliaOn, setJuliaOn] = useState(true);
    const [rainOn, setRainOn] = useState(true);
    const [sRocketsOn, setSRocketsOn] = useState(true);


    const PlaySnake = () => {
        setSCartOn(true);
        setSRocketsOn(true);
        setRainOn(true);
        setJuliaOn(true);
        setSnakeOn(false); 
        let sn = document.getElementsByClassName("Snake")[0];
        sn.scrollIntoViewIfNeeded();
    }

    const PlayJulia = () => {
        setSCartOn(true);
        setSRocketsOn(true);
        setRainOn(true);
        setSnakeOn(true);
        setJuliaOn(false); 
        let jl = document.getElementsByClassName("Julia")[0];
        jl.scrollIntoViewIfNeeded();
    }

    const PlayRain = () => {
        setSCartOn(true);
        setSRocketsOn(true);
        setSnakeOn(true);
        setJuliaOn(true); 
        setRainOn(false); 
        let rn = document.getElementsByClassName("Rain")[0];
        rn.scrollIntoViewIfNeeded();
    }
    
    const PlaySRockets = () => {
        setSCartOn(true);
        setSnakeOn(true);
        setJuliaOn(true); 
        setRainOn(true); 
        setSRocketsOn(false);
        let sr = document.getElementsByClassName("SmartRockets")[0];
        sr.scrollIntoViewIfNeeded();
    }

    const PlaySCart = () => {
        setSnakeOn(true);
        setJuliaOn(true); 
        setRainOn(true); 
        setSRocketsOn(true);
        setSCartOn(false);
        let sr = document.getElementsByClassName("sc-display")[0];
        sr.scrollIntoViewIfNeeded();
    }


    return(
        <div className="Storage">

            <h1 className="Title">Storage</h1>
            <p>
                This store along with the website is built to display my work on front-end components 
                and design patterns. All listed implementations are carefuly carried out to solidify my expertise.
                Different components were implemented with different frameworks and libraries, but they are all gathered to this website 
                which is written using React.js. Please use the buttons to activate a component
            </p>
            <div className="Container">

            <div className="displayItem">
                    <div className="Julia">
                        { juliaOn ? <button onClick={()=>{PlayJulia()}}>Julia Set, Visual Demonstration</button> : <Julia /> }
                        <br/><span>Mouse position on the image will display the span of this mathematical funtion.<br/> As in, Image = f(mouseX, mouseY)</span>
                    </div>
                </div>
                <div className="displayItem">
                    <div className="Snake">
                        { snakeOn ? <button onClick={()=>{PlaySnake()}}>Snake Game</button> : <Snake /> }
                        <br/><span>Play the game, use 'A','S','D','W' keys to move the snake. Aim is to eat the food and survive as long as possible</span>
                    </div>
                </div>
                <div className="displayItem">
                    <div className="sc-display">
                    { sCartOn ? <button onClick={()=>{PlaySCart()}}>ShoppingCart</button> : <Shoppingcart initialItems={items}/> }
                    <br/><span>A shopping cart using client storage</span>
                    </div>
                </div>
                <div className="displayItem">
                    <div className="Rain">
                        { rainOn ? <button onClick={()=>{PlayRain()}}>Rain Animation</button> : <Rain /> }
                        <br/><span>Static rain animation with changing quotes inside. Drops are JS objects with coordinate parameters. Gravity, wind, small scale motion equations applied. And lightning!</span>
                    </div>
                </div>
                <div className="displayItem">
                    <div className="SmartRockets">
                        { sRocketsOn ? <button onClick={()=>{PlaySRockets()}}>Smart Rockets</button> : <SmartRockets /> }
                        <br/><span>JS objects rockets, learning through generations using genetic algorithm. The aim is to hit the target. Force vector per frame constitudes their genes</span>
                    </div>
                </div>
                

            </div>
            

        </div>
    )
}

export default Storage