import React from 'react'
import Sketch from 'react-p5'

let width = 600, height = 600;

class Snake {
    constructor() {
        this.x = width / 2;
        this.y = height / 2;
        this.speed = 50;
        this.xSpeed = -this.speed;;
        this.ySpeed = 0;
        this.foodX = Math.floor(Math.random() * width / this.speed) * this.speed;
        this.foodY = Math.floor(Math.random() * height / this.speed) * this.speed;
        this.tail = [];
        this.total = 0;
        this.score = 0;
    }

    show(p5) {
        var start = 20;
        p5.fill(10, 200, 50, 150);
        p5.rect(this.x, this.y, this.speed, this.speed);
        p5.fill(10, 200, 50, start);
        for (var i = 0; i < this.tail.length; i++) {
        start = Math.floor(start + (150 - start) / 8);
        p5.stroke(50, 50, 50, start);
        p5.fill(10, 200, 50, start);
        p5.rect(this.tail[i].x, this.tail[i].y, this.speed, this.speed);
        p5.stroke(10, 200, 50, 50);
        p5.fill(10, 200, 50, 50);
        p5.ellipse(this.tail[i].x + this.speed / 10 * (Math.floor(Math.random() * 7) + 1),
            this.tail[i].y + this.speed / 10 * (Math.floor(Math.random() * 7) + 1),
            this.speed / 5, this.speed / 5);
            p5.stroke(50, 50, 50, start / 3);
        }
        p5.fill(200, 10, 10, 150);
        p5.ellipse((this.foodX + this.speed / 2), (this.foodY + this.speed / 2), (this.speed, Math.floor(this.speed * 0.9)), (this.speed, Math.floor(this.speed * 0.8)));
    }
    update(p5) {
        for (var i = 0; i < this.tail.length - 1; i++) {
        this.tail[i] = this.tail[i + 1];
        }
        if (this.total > 0) {
        this.tail[this.total - 1] = p5.createVector(this.x, this.y, 0);
        }
    
        this.x += this.xSpeed;
        this.y += this.ySpeed;
        this.x = (this.x + width) % width;
        this.y = (this.y + height) % height;
    
    
    }
    move(x, y) {
      if (!(this.xSpeed * x < 0))
        this.xSpeed = x * this.speed;
      if (!(this.ySpeed * y < 0))
        this.ySpeed = y * this.speed;
    }
    workFood(time, p5) {
        var done = false;
        p5.fill(10);
        p5.textAlign(p5.CENTER);
        p5.text(Math.floor(this.score), width * 9 / 10, height * 9 / 10);
        if (p5.dist(this.x, this.y, this.foodX, this.foodY) < 1) {
            while (!done) {
                this.foodX = Math.floor(Math.random() * width / this.speed) * this.speed;
                this.foodY = Math.floor(Math.random() * height / this.speed) * this.speed;
                done = true;
                for (var i = 0; i < this.tail.length; i++) {
                    if (p5.dist(this.tail[i].x, this.tail[i].y, this.foodX, this.foodY) < 1)
                        done = false;
                }
                if (p5.dist(this.x, this.y, this.foodX, this.foodY) < 1) {
                    done = false;
                }
                this.score += 100 / time;
            }
        this.total++;
        return true;
    } else return false;
    }
    isDead(p5) {
        for (var i = 0; i < this.tail.length; i++) {
        if (p5.dist(this.x, this.y, this.tail[i].x, this.tail[i].y) < 1)
            return true;
        }
        return false;
    }
}

export default () => {
    var snake;
    var keyPresses = []
    var q = 0;
    var gameOver = false;
    var frameCount = 0;
    var time = 0;

    const setup = (p5, parent) => {
        p5.createCanvas(600, 600).parent(parent);
        reset();
        p5.frameRate(5);

        p5.textSize(40);
        p5.textAlign(p5.CENTER, p5.CENTER);
    }

    const draw = p5 => {
        keyPressed(p5);
        time++;
        frameCount++;
        p5.background(150);
        p5.fill(200, 10, 10, 100);
        p5.stroke(50, 50, 50);
        if (gameOver) {
            p5.fill(10);
            p5.textAlign(p5.CENTER);
            p5.text("Find Deniz ask for 20 Euros. \nWell played!", width / 2, height / 2);
            return;
        }
        if (keyPresses[0] !== undefined) {
            snake.move(keyPresses[0].x, keyPresses[0].y);
            keyPresses.shift();
            q--;
            if (frameCount === 3) {
                keyPresses = [];
                q = 0;
                frameCount = 0;
            }
        } 
        snake.update(p5);

        var zero = snake.workFood(time, p5);
        if (zero) time = 0;
        if (snake.isDead(p5)) {
            reset();
        }

        snake.show(p5);
        if (snake.tail.length + 2 === (width * height / snake.speed / snake.speed)) {
            gameOver = true;
        }
    }

    function reset() {
        snake = new Snake();
    }


    function keyPressed(p5) {
        frameCount = 0;
        if (p5.keyCode === 65) {
            keyPresses[q] = p5.createVector(-1, 0);
            q++;
        } else if (p5.keyCode === 68) {
            keyPresses[q] = p5.createVector(1, 0);
            q++;
        } else if (p5.keyCode === 87) {
            keyPresses[q] = p5.createVector(0, -1);
            q++;
        } else if (p5.keyCode === 83) {
            keyPresses[q] = p5.createVector(0, 1);
            q++;
        }else{
            return;
        }
    }

    return <Sketch setup={setup} draw={draw} />
    
}

